import CONFIG from '../../globals/config'

const createLikePlaceButtonTemplate = () => `
  <button aria-label="like this place" id="likeButton" class="like">
     <i class="fa fa-heart-o" aria-hidden="true"></i>
  </button>
`

const createUnlikePlaceButtonTemplate = () => `
  <button aria-label="unlike this place" id="likeButton" class="like">
    <i class="fa fa-heart" aria-hidden="true"></i>
  </button>
`

const createListMenus = (data) => {
  const list = document.createElement('ul')
  list.className = 'sub-list'
  data.forEach((value) => {
    list.innerHTML += `<li>${value.name}</li>`
  })
  return list.outerHTML
}

const createListReviews = (data) => {
  const review = document.createElement('ul')
  review.className = 'card-detail-reviews'
  review.innerHTML += '<h3>Reviews</h3>'
  data.forEach((value) => {
    review.innerHTML += `
    <li>
      <h4 class="reviews-name">${value.name}</h4>
      <p class="reviews-date">${value.date}</p>
      <p class="reviews-body"><em>${value.review}</em></p>
    </li>
    `
  })
  return review.outerHTML
}

const createInputFrom = () =>
  `
    <form class="input_form">
      <input type="text" placeholder="Nama" id="input_name"></input>
      <textarea name="input_reviews" placeholder="Masukkan Reviews" id="input_reviews"></textarea>
      <button type="submit" class="input-review-btn">Kirim Review</button>
    </form>
    `

const createCardItemTemplate = (card) => `
  <div class="card-item">
    <div class="card-image">
        <img
            class="lazyload"
            src="${CONFIG.BASE_IMAGE_URL + card.pictureId}"
            alt="${card.name}"
        />
        <p class="card-city">${card.city}</p>
    </div>
    <div class="card-body">
        <h3><a href="/#/detail/${card.id}" class="card-body-title">${
  card.name
}</a></h3>
        <h4>Rating : <span class="card-rating">${card.rating}â˜…</span></h4>
        <p>
            ${card.description}
        </p>
    </div>
  </div>
`

const createCardDetailTemplate = (card) => `
<h2 class="detail-title">${card.name}</h2>
  <div class="movie-detail">
    <div class="card-detail-header">
    <div class="card-image">
        <img
            src="${CONFIG.BASE_IMAGE_URL + card.pictureId}"
            alt="${card.name}"
        />
        <p class="card-city">${card.city}</p>
    </div>
    <ul class="card-detail-body">
      <li class="card-detail-body-list">
        <h3>Alamat</h3>
        <p> ${card.address}</p>
      </li>
      <li class="card-detail-body-list">
        <h3>Foods</h3>
        ${createListMenus(card.menus.foods)}
      </li>
     <li class="card-detail-body-list">
        <h3>Drinks</h3>
        ${createListMenus(card.menus.drinks)}
      </li>
    </ul>
    </div>
    ${createListReviews(card.customerReviews)}
    ${createInputFrom()}
  </div>
`

export {
  createCardItemTemplate,
  createCardDetailTemplate,
  createLikePlaceButtonTemplate,
  createUnlikePlaceButtonTemplate
}
